<template>
  <div class="back">
    <el-row class="nav">
      <el-col :span="4" align="middle">
        <div @click="toHome">
          <svg style="margin-left:60px" t="1608126764083" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="14621" width="32" height="32"><path d="M1006.1 412.7l-187.5-141c0-0.2 0.1-0.3 0.1-0.4V121.4c0-2.3-1.9-4.2-4.2-4.2h-92.4c-2.3 0-4.2 1.9-4.2 4.2V196L535.3 58.8c-7.3-5.5-16-8.4-25.1-8.4-9.2 0-17.9 2.9-25.2 8.5L16.7 412.5C7.8 419.2 2.1 429 0.5 440.1c-2.1 14.8 3.8 29.5 16.2 39.3 4.3 3.3 9.2 5.7 14.5 7 13 3.2 25.8 0.6 36-7.1L505 148.6c3.1-2.3 7.4-2.3 10.4 0l441.8 332c7.3 5.5 16 8.4 25.1 8.4 13.7 0 26.3-6.5 34.2-17.7 13.3-18.8 7.9-44.9-10.4-58.6z" fill="#1296db" p-id="14622"></path><path d="M906.7 499.4l-193.2-140-196.7-142.5c-3.4-2.5-8.1-2.5-11.5 0L308.7 359.4l-193.2 140c-5.6 4.1-9 10.6-9 17.6v392.1c0 35.5 29 64.5 64.5 64.5h246.7V716.2c0-30 24.6-54.6 54.6-54.6h77.5c30 0 54.6 24.6 54.6 54.6v257.4h246.7c35.5 0 64.5-29 64.5-64.5V517c0.1-6.9-3.3-13.5-8.9-17.6z" fill="#1296db" p-id="14623"></path></svg>
        </div>
      </el-col>
      <el-col :span="14">
        <div>
          <div class="el-input" style="width:90%">
            <input style="border-radius: 0px" type="text" v-model="input" @keyup.enter="search"
            autocomplete="off" placeholder="请输入内容" class="el-input__inner">
          </div>
          <el-button type="danger"
          style="width: 10%;border-radius:0px;
          border-bottom: 0px;min-width:80px"
           icon="el-icon-search" @click="search">搜索</el-button>
        </div>
      </el-col>
      <el-col :span="1" :offset="1">
        <!-- 徽章：从本地缓存中获取 -->
        <el-badge :value="cartNumber" class="item">
          <el-button @click="handleCartClick"
          style="margin-top: 4px" size="small" type="primary">购物车</el-button>
        </el-badge>
      </el-col>
      <el-col :span="2" :offset="1">
        <span ref="user" style="margin-top: 5px;display:block">
          <div v-show="loginName == ''">
            <router-link to="/login">登录/</router-link>
            <router-link to="/register">注册</router-link>
          </div>
          <div v-show="loginName != ''" style="display:inline">
            <span>hi,{{loginName}}</span>
            <el-button @click="logout"
            type="text" style="margin-left:5px">注销</el-button>
          </div>
        </span>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="16" :offset="4" style="padding-bottom: 1%;">
        <span style="color: red">每日推荐</span>
        <router-link to="#" style="margin-left: 5px;text-decoration:none"
        v-for="(item, i) in recommend" :key="i">
          {{item}}
        </router-link>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import Service from '../axios/http';
import cartUtil from '../utils/Cart';

export default {
  props: ['name'],
  data() {
    return {
      input: '',
      recommend: [],
      cartNumber: 0,
      loginName: '',
    };
  },
  methods: {
    search() {
      if (this.input == null) {
        return;
      }
      this.$router.push(`/productList/${this.input}`);
      this.$emit('update');
    },
    logout() {
      localStorage.setItem('user', '');
      this.$router.push('/');
    },
    toHome() {
      this.$router.push('/');
    },
    handleCartClick() {
      this.$router.push('/shopCart');
    },
  },
  created() {
    Service('recommend').then((res) => {
      this.recommend = res.data.data;
    });
    // 初始化
    this.cartNumber = cartUtil.getCart().length;
    this.input = this.name;
  },
  mounted() {
    this.loginName = localStorage.getItem('user') || '';
    // 监听localStorage的set事件
    const that = this;
    window.addEventListener('setItemEvent', (e) => {
      const username = localStorage.getItem('user');
      if (e.key === `cart${username}`) {
        that.cartNumber = JSON.parse(e.value).length;
      }
      if (e.key === 'user') {
        that.loginName = e.value;
      }
    });
  },
};
</script>
<style scoped>
.back {
  padding: auto;
  width: 100%;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  background-color: white;
  margin-bottom: 20px;
}
.nav {
  padding-top: 1%;
};
</style>
